import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import * as _ from 'lodash-es';
import { Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { PropertiesService } from '../properties.service';
import { Trace } from './model/trace';

@Injectable({
  providedIn: 'root'
})
export class TrackingService {
  tracesMockCache: Trace[];

  constructor(private http: HttpClient) { }

  get(domainId: string, traceType: string, objectId: string, startTimestamp: string): Observable<Trace[]> {    
    let host = PropertiesService.properties.aurionTrack;
    return this.http.get<Trace[]>(`${host}/trace/${domainId}/${objectId}/${traceType}?startTimestamp=${startTimestamp}`)
  }

  getByType(domainId: string, traceType: string): Observable<Trace[]> {
    let host = PropertiesService.properties.aurionTrack;
    return this.http.get<Trace[]>(`${host}/state/${domainId}/${traceType}`)
  }

  getLatestByDomainsAndTag(domains: string[], tag: string) {
    if (PropertiesService.properties.trackingsmock) {
      return this.http.get<Trace[]>(PropertiesService.properties.trackingsmock).pipe(map(traces => {
        traces = traces.filter(item => domains.indexOf(item.domainId) != -1).filter(item => item.tags?.find(itemTag => itemTag == tag));
        let grouped = _.groupBy(traces, trace => trace.objectId);
        let states = Object.keys(grouped).map(key => grouped[key]).map(group => group.sort((o1, o2) => o2.timestamp - o1.timestamp)[0]);
        return states;
      }));
    }
  }

  set(trace: Trace): Observable<any> {
    // if (PropertiesService.properties.trackingsmock) {
    //   if (!this.tracesMockCache) {
    //     this.tracesMockCache = [];
    //   }
    //   let idx = this.tracesMockCache.findIndex(item => item.objectId == trace.objectId);
    //   if (idx != -1) {
    //     this.tracesMockCache.splice(idx, 0, trace);
    //   } else {
    //     this.tracesMockCache.push(trace);
    //   }
    //   return of(1);
    // } else {
      let host = PropertiesService.properties.aurionTrack;
      return this.http.post<Trace>(`${host}/trace`, trace);
    // }
  }

}
