import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PropertiesService } from '../properties.service';
import { Event } from './model/event';

@Injectable({
  providedIn: 'root'
})
export class EventService {

  constructor(private http: HttpClient) {
  }

  getEventsBySubject(domainId: string, subject: string, startTimestamp: number, search?: string): Observable<Event[]> {
    let host = PropertiesService.properties.aurionEvent;
    let url = `${host}/event/subject/${domainId}/${subject}?startTimestamp=${startTimestamp}`;
    if(search != null){
      url += "&search=" + search ;
    }
    return this.http.get<Event[]>(url);
  }

  getEvents(domainId: string, objectId: string, startTimestamp: number): Observable<Event[]> {
    let host = PropertiesService.properties.aurionEvent;
    let url = `${host}/event/object/${domainId}/${objectId}?startTimestamp=${startTimestamp}`;
    return this.http.get<Event[]>(url);
  }

}