import { Injectable } from '@angular/core';
import * as auth0 from 'auth0-js';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { PropertiesService } from './properties.service';

@Injectable({
  providedIn: 'root'
})
export class Auth0AuthService {
  innerwebAuth: auth0.WebAuth;
  user;

  extraRedirectUri;

  constructor() {
  }

  private get webAuth(): auth0.WebAuth {
    if (this.innerwebAuth == null) {
      this.innerwebAuth = new auth0.WebAuth({
        clientID: PropertiesService.properties.auth0.clientId,
        domain: PropertiesService.properties.auth0.domain,
        scope: 'openid profile email current_user',
        responseType: 'token id_token code',
        redirectUri: PropertiesService.properties.auth0.redirectUri + (this.extraRedirectUri ? this.extraRedirectUri : '')
      });
    }
    return this.innerwebAuth;
  }

  authenticate(username: string, password: string, redirectUri?: string): Observable<boolean> {
    this.extraRedirectUri = redirectUri;
    let behaviorSubject = new BehaviorSubject<boolean>(false);
    this.webAuth.login({
      realm: PropertiesService.properties.auth0.realm,
      username: username,
      password: password
    }, (err, res) => {
      if (err != null) {
        console.error('UsuÃ¡rio e/ou senha errados.');
        throwError('UsuÃ¡rio e/ou senha errados.');
      }
      this.user = res;
      console.log(res);
      behaviorSubject.next(true);
    });
    return behaviorSubject;
  }

  public forgotPassword(email: string): Observable<boolean> {
    let behaviorSubject = new BehaviorSubject<boolean>(false);
    this.webAuth.changePassword({
      connection: PropertiesService.properties.auth0.realm,
      email
    }, (err, resp) => {
      if (err) {
        console.error(err);
        throwError(JSON.stringify(err));
      }
      behaviorSubject.next(true);
    });
    return behaviorSubject;
  }

  public async handleAuthentication(): Promise<any> {
    //this.setIsOnHandleAuthenticationCallback(true);

    await new Promise((resolve, reject) => {
      this.webAuth.parseHash((err, authResult) => {
        console.log(authResult);
        if (err) {
          console.error(err)
          reject(err);
        } else {
          resolve(authResult);
        }
      });
    }).then((authResult: any) => {
      console.log(authResult)
      if (authResult && authResult.accessToken && authResult.idToken) {
        this.setUser(authResult)
      }
    })
      .catch((err) => {
        console.error(err);
        /* this.loaderService.hideLoader();
        this.router.navigate(['/']);
        this.alertService.showMessage(err.errorDescription);
        return Promise.reject(err); */
      });
  }

  private setUser(user) {
    this.user = user;
    sessionStorage.setItem('user', JSON.stringify(user));
  }

  getUser() {
    let userJSON = sessionStorage.getItem('user');
    if (userJSON != null && userJSON != '') {
      this.user = JSON.parse(userJSON);
    }
    return this.user;
  }

  isUserLoggedIn(): boolean {
    let user = this.getUser();
    return user != null;
  }

  logout() {
    this.user = null;
    sessionStorage.removeItem('user');
  }

}